<template>
	<div id="home-index">
		<public-header title="首页">
			<div slot="right" class="flex">
				<i class="icon icon-people" @click="confirmDialog = true"></i>
			</div>
		</public-header>
		<div class="content">
			<div class="tabs flex-lr">
				<div class="tabs-item" :class="{active: filter.items.status == 3}" @click="onToggleStatus(3)">
					<div class="title">新订单 <i class="icon icon-filter"></i></div>
				</div>
				<div class="tabs-item" :class="{active: filter.items.status == 7}" @click="onToggleStatus(7)">
					<div class="title">待取货</div>
				</div>
				<div class="tabs-item" :class="{active: filter.items.status == 4}" @click="onToggleStatus(4)">
					<div class="title">配送中</div>
				</div>
			</div>
			<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
			<van-list
				v-model="records.loading"
				:finished="records.finished"
				@load="onLoad"
				:offset="100"
				:immediate-check="false"
				v-if="!records.empty"
				class="order-list"
			>
				<div class="order-item" v-for="(item, index) in records.data" :key="item.id">
					<div class="flex-lr padding-10-b">
						<div class="flex font-14">
							<span class="icon icon-time font-16 padding-5-r"></span>
							<div class="c-danger flex" v-if="item.status == 4 && item.delivery_status != 3">
								<template v-if="item.delivery_overtime > 0">
									距超时<count-down :endTime="item.delivery_overtime" class="c-danger"></count-down>
								</template>
								<template v-else-if="item.delivery_overtime_start > 0">
									已超时<count-down :startTime="item.delivery_overtime_start" class="c-danger"></count-down>
								</template>
							</div>
							<div class="c-primary" v-else>{{item.addtime_cn}}下单</div>
						</div>
						<div class="c-danger font-14">
							<span class="font-18">{{item.plateform_deliveryer_fee}}</span>元
						</div>
					</div>
					<van-row class="padding-10-b">
						<van-col span="4">
							<div class="address-left">
								<div>{{item.store2deliveryer_distance}}km</div>
								<div class="padding-5-t">购买</div>
							</div>
						</van-col>
						<van-col span="20">
							<div class="address-right">
								{{item.store.title}}
							</div>
						</van-col>
					</van-row>
					<van-row class="padding-10-b">
						<van-col span="4">
							<div class="address-left">
								<div>{{item.store2user_distance}}km</div>
								<div class="padding-5-t">送货</div>
							</div>
						</van-col>
						<van-col span="20">
							<div class="address-right">
								{{item.address}}
							</div>
						</van-col>
					</van-row>
					<div class="order-info">
						<div class="itag itag-danger" style="background-color: #a55ede">帮买</div>
						<div class="itag">餐饮<span class="padding-5-l">小于5kg</span></div>
					</div>
					<div class="remark" v-if="item.note">
						备注：{{item.note}}
					</div>
					<ul class="padding-15-t padding-5-b flex-lr">
						<template v-if="item.delivery_status == 3">
							<li class="btn-item success" @click="onChangeOrderStatus(index, 'delivery_assign')">抢单</li>
						</template>
						<template v-if="item.delivery_status == 4 || item.delivery_status == 7 || item.delivery_status == 8">
							<template v-if="item.transfer_delivery_status == 1">
								<li class="btn-item bg-danger" @click="onChangeOrderStatus(index, 'direct_transfer_refuse')">拒绝转单</li>
								<li class="btn-item success" @click="onChangeOrderStatus(index, 'direct_transfer_agree')">接受转单</li>
							</template>
							<template v-else>
								<template v-if="item.delivery_status == 4">
									<li class="btn-item bg-warning" v-if="item.mobile" @click="util.jsTel(item.mobile)">联系顾客</li>
									<li class="btn-item success" @click="onChangeOrderStatus(index, 'delivery_success')">确认送达</li>
								</template>
								<template v-else>
									<li class="btn-item" v-if="item.store && item.store.telephone" @click="util.jsTel(item.store.telephone)">联系商家</li>
									<li class="btn-item success" @click="onChangeOrderStatus(index, 'delivery_takegoods')">确认取货</li>
								</template>
							</template>
						</template>
					</ul>
				</div>
			</van-list>
			<div class="no-data" v-else>
				<img src="static/img/order_no_date.png" alt="">
				<template v-if="filter.items.status == 3">
					<template v-if="deliveryer.work_status == 1">
						<template v-if="deliveryer.perm_takeout > 0">
							<template v-if="!can_collect_order">
								<p >当前调度模式不允许抢单,请等待管理员或系统派单</p>
							</template>
							<template v-else>
								<p>暂无待抢的订单</p>
							</template>
						</template>
						<template v-else>
							<p>您没有外卖配送权限，请联系管理员</p>
						</template>
					</template>
					<template v-else>
						<p>您当前处于收工状态,不能抢单</p>
					</template>
				</template>
				<template v-else>
					<p>暂无符合条件的订单</p>
				</template>
			</div>
			</van-pull-refresh>
			<div class="refresh-button">
				<van-button size="normal" block class="font-16" @click="onPullDownRefresh()"><span class="icon icon-refresh"></span>刷新列表</van-button>
			</div>
			<!--提示文字-->
			<div class="home-tips" @click="util.$toast('同时接单量最大为三单')">同时接单量受限</div>
			<!--左侧弹出层-->
			<van-popup v-model="confirmDialog" position="left" :overlay="true">
				<div class="pop-header">
					<div class="nickname flex">你好，{{deliveryer.title}}<div class="icon icon-right padding-5-l"></div></div>
					<div class="itag user-grade" v-if="1" style="color: #5d4837; border-color: #ffca2e">青铜3</div>
				</div>
				<div class="pop-list">
					<div class="pop-item flex-lr">
						<div class="pop-left font-16">上线</div>
						<div class="pop-right">
							<iswitch :value="deliveryer.work_status" condition-open="1" condition-close="0" slot="right-icon" text-open=" " text-close=" " @change="jsToggleSwitch" keys="deliveryer.work_status"></iswitch>
						</div>
					</div>
					<div class="pop-item flex-lr">
						<router-link tag="div" to="/pages/finance/current" class="pop-left font-16">明细</router-link>
					</div>
					<div class="pop-item flex-lr">
						<div class="pop-left font-16">我的账户</div>
						<div class="pop-right">
							<div class="account itag">¥{{deliveryer.credit2}}</div>
						</div>
					</div>
					<div class="pop-item flex-lr hide">
						<router-link tag="div" to="/pages/finance/index" class="pop-left font-16">资产</router-link>
					</div>
					<div class="pop-item flex-lr">
						<router-link tag="div" to="/pages/comment/list" class="pop-left font-16">评价</router-link>
					</div>
					<div class="pop-item flex-lr">
						<router-link tag="div" to="/pages/statcenter/index" class="pop-left font-16">工作统计</router-link>
					</div>
					<div class="pop-item flex-lr">
						<router-link tag="div" to="/pages/member/phonic" class="pop-left font-16">语音设置</router-link>
					</div>
					<div class="pop-item flex-lr">
						<router-link tag="div" to="/pages/finance/getcashList" class="pop-left font-16">帮助中心</router-link>
					</div>
					<div class="pop-item flex-lr">
						<router-link tag="div" to="/pages/member/setting" class="pop-left font-16 flex">
							设置
							<div class="itag setting" v-if="1"  style="background-color: #ffca2e; color: #5d4837">填资料更安心</div>
						</router-link>
					</div>
				</div>
				<div class="icon icon-fold c-gray w-100"></div>
				<van-row class="padding-15">
					<van-col span="8">
						<router-link tag="div" to="/pages/order/takeout" class="pop-bottom">
							<div class="icon icon-emojifill" style="color: #5d4837; font-size: 30px"></div>
							<div>微笑行动</div>
						</router-link>
					</van-col>
					<van-col span="8">
						<router-link tag="div" to="/pages/paotui/index" class="pop-bottom">
							<div class="icon icon-community_fill_light" style="color: #5d4837; font-size: 30px"></div>
							<div>骑手社区</div>
						</router-link>
					</van-col>
				</van-row>
			</van-popup>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import countDown from "@/components/countDown"
	import {getRecords} from "../../controller/funcCommon"
	import {changeOrderStatus} from "../order/controller/order.js"
	import iswitch from "@/components/switch"
	export default {
		data() {
			return {
				filter: {
					items: {
						status: 3
					}
				},
				isRefresh: false,
				showPreLoading: true,
				records:{
					page: 1,
					psize: 15,
					loading: false,
					finished: false,
					empty: false,
					data:[]
				},
				deliveryer: {},
				can_collect_order: false,
				config: {},
				confirmDialog: false
			}
		},
		components: {
			publicHeader,
			countDown,
			iswitch
		},
		methods: {
			onToggleStatus(value){
				var that = this;
				value = parseInt(value);
				if(that.filter.items.status == value) {
					return;
				}
				that.filter.items.status = value;
				that.onLoad(true);
			},
			onLoad(force){
				var that = this;
				getRecords({
					vue: that,
					force: force,
					url: 'delivery/order/takeout/list',
					recordsName: 'orders',
					success: function(result) {
						that.deliveryer = result.deliveryer;
						that.can_collect_order = result.can_collect_order;
						that.config = result.config;
					}
				});
			},
			jsToggleSwitch(data) {
				var that = this;
				that.util.jsToggleSwitch({
					vue: that,
					key: data.keys,
					value: data.value,
					url: 'delivery/member/mine/setting',
					data: {
						which: 'work_status',
					}
				});
			},
			onChangeOrderStatus(index, type) {
				var that = this;
				changeOrderStatus({
					vue: that,
					type: type,
					index: index,
					from: 'list',
					order: that.records.data[index],
				})
			},
			onPullDownRefresh() {
				this.onLoad(true);
			},
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#home-index #public-header>div{
		background-color: #262A36!important;
	 }
	#home-index .icon-people{
		font-size: 18px;
	}
	#home-index .tabs{
		background-color: #262A36;
		color: #fff;
		padding: 0 10px;
		position: fixed;
		z-index: 100;
		top: 46px;
		left: 0;
		right: 0;
	}
	#home-index .tabs .tabs-item{
		width: 20%;
		display: flex;
		flex-flow: column nowrap;
		align-items: center;
		justify-content: space-between;
		position: relative;
	}
	#home-index .tabs .tabs-item .title{
		font-size: 14px;
		font-weight: bold;
		margin-bottom: 15px;
	}
	#home-index .tabs .tabs-item.active:after{
		content: '';
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		height: 4px;
		background-color: #fff;
	}
	#home-index .order-list{
		padding-top: 29px;
	}
	#home-index .order-list .order-item{
		margin-top: 10px;
		padding: 10px 15px;
		background-color: #fff;
	}
	#home-index .order-list .order-item:last-child{
		margin-bottom: 70px;
	}
	#home-index .order-list .order-item .address-left{
		font-size: 12px;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	#home-index .order-list .order-item .address-right{
		flex: 1;
		font-size: 18px;
		font-weight: bold;
		padding-left: 10px;
		padding-bottom: 10px;
	}
	#home-index .order-list .order-item .order-info{
		padding-left: 12.5%;
	}
	#home-index .order-list .order-item .order-info div:first-child{
		font-size: 14px;
	}
	#home-index .order-list .order-item .order-info div:last-child{
		background-color: #fff;
		color: #458BF8;
		border: 1px solid #C6CFFF;
		font-size: 14px;
	}
	#home-index .order-list .order-item .remark{
		margin-left: 12.5%;
		margin-top: 10px;
		padding: 5px 10px;
		background-color: #f5f5f5;
		border-radius: 5px;
		font-size: 12px;
		line-height: 22px;
	}
	#home-index .order-list .order-item .btn-item{
		flex: 1;
		height: 44px;
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 5px;
		font-size: 15px;
		color: #fff;
		background-color: #262A36;
	}
	#home-index .order-list .order-item .btn-item:nth-child(2){
		margin-left: 10px;
	}
	#home-index .refresh-button{
		position: fixed;
		bottom: 0;
		left: 0;
		right: 0;
		width: 100%;
		height: 60px;
		background-color: #FAFAFA;
		padding: 10px 15px;
	}
	#home-index .refresh-button .icon-refresh{
		padding-right: 10px;
		color: #333;
		font-size: 16px;
		font-weight: bold;
	}
	#home-index .refresh-button button{
		height: 40px;
		line-height: 40px;
		border: 1px solid #e0e0e0;
		background-color: #fff;
		color: #333;
	}
	#home-index .van-popup--left{
		width: 80%;
		height: 100%;
	}
	#home-index .pop-header{
		padding: 15px;
		border: 1px solid #e5e5e5;
	}
	#home-index .pop-header .user-grade{
		color: #0EC3B3;
		background-color: #fff;
		border: 1px solid #0EC3B3;
		border-radius: 25px;
		margin-top: 10px;
		padding: 2px 10px;
	}
	#home-index .pop-header .nickname{
		font-size: 20px;
		font-weight: 500;
		height: 50px;
	}
	#home-index .pop-list{
		padding: 15px;
	}
	#home-index .pop-list .pop-item{
		padding: 10px 0;
		height: 50px;
	}
	#home-index .pop-list .pop-item .pop-left{
		text-align: left;
		width: 60%;
	}
	#home-index .pop-list .pop-item .pop-right{
		width: 40%;
		text-align: right;
	}
	#home-index .pop-list .pop-item .pop-right .iswitch{
		border: 1px solid #fff;
		border-radius: 30px;
		width: 55px;
	}
	#home-index .pop-list .pop-item .pop-right .iswitch .block{
		border-radius: 30px;
	}
	#home-index .pop-list .pop-item .pop-right .iswitch .text{
		font-size: 13px;
	}
	#home-index .pop-list .pop-item .pop-right .account{
		border-radius: 23px;
		border: 1px solid #333;
		color: #333;
		background-color: #fff;
		font-size: 12px;
	}
	#home-index .pop-list .pop-item .pop-left .setting{
		color: #fff;
		background-color: #0EC3B3;
		padding: 2px 10px;
		border-radius: 21px;
		margin-left: 10px;
	}
	#home-index .icon-fold{
		text-align: center;
		font-size: 22px;
	}
	#home-index .pop-bottom{
		font-size: 14px;
		text-align: center;
	}
	#home-index .pop-bottom div:first-child{
		font-size: 24px;
		color: #0EC3B3;
		padding-bottom: 10px;
	}
	#home-index .btn-item.success{
		background-color: #06A936!important;
	}
	#home-index .no-data img{
		padding: 10px;
		border-radius: 100%;
		background-color: #fff;
	}
	#home-index .home-tips{
		position: fixed;
		right: 0;
		bottom: 90px;
		padding: 13px 10px;
		color: #fff;
		font-size: 14px;
		background: linear-gradient(to right, #FE5605 , #FF8031);
		border-radius: 34px 0 0 34px;
		z-index: 2007;
	}
</style>


